.canvasContainer {
  padding: 5px;
  width: 100%;
  background-color: #fff;
}
.canvasContainer .editor {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  width: 100%;
  height: 600px;
  background: #fff;
}
.canvasContainer .editorHd {
  padding: 8px;
  border: 1px solid #e6e9ed;
}
.canvasContainer .editorBd {
  flex: 1 1;
}
.canvasContainer .editorSidebar,
.canvasContainer .editorContent {
  display: flex;
  flex-direction: column;
}
.canvasContainer .editorSidebar {
  background: #fff;
  height: 600px;
}
.canvasContainer .editorSidebar:first-child {
  border-right: 1px solid #e6e9ed;
}
.canvasContainer .editorSidebar:last-child {
  border-left: 1px solid #e6e9ed;
}
.canvasContainer .flow,
.canvasContainer .mind,
.canvasContainer .koni {
  flex: 1 1;
}
