.templateWrap {
  min-height: calc(100%);
  background-color: #fff;
  padding: 10px;
}
.templateWrap .buttonWrap {
  width: 100%;
  margin-bottom: 10px;
}
.templateWrap .buttonWrap button {
  margin-right: 10px;
}
.templateWrap .cardMeta {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.templateWrap .templateContent {
  display: flex;
  flex-direction: row;
}
.templateWrap .templateContent .listWrap {
  width: 300px;
  border-right: 1px solid #e6e9ed;
}
.templateWrap .templateContent .listWrap .ant-tabs-bar {
  margin: 0;
}
.templateWrap .templateContent .listWrap .ant-list-pagination {
  margin-top: 10px;
}
.templateWrap .templateContent .ggeditorWrap {
  width: calc(100% - 220px);
  height: calc(100vh - 230px);
}
.descTitle {
  margin: 30px 0 10px 0;
  border-left: 5px solid #1890ff;
  text-indent: 1em;
  font: 16px bold;
}
.descTitleFirst {
  margin-bottom: 10px;
  border-left: 5px solid #1890ff;
  text-indent: 1em;
  font: 16px bold;
}
.ant-popover-inner-content p:hover {
  color: #1890ff;
}
.ant-col-12 .anticon {
  cursor: pointer;
  position: absolute;
  right: 3px;
  bottom: 35px;
  font-size: 16px;
  color: #999;
  transition: all 0.3s;
}
.canvasContainer {
  padding: 5px;
  height: 100%;
  width: 100%;
  background-color: #fff;
}
.canvasContainer .pageheader {
  width: 100%;
  border: 1px solid #ebedf0;
}
.canvasContainer .editor {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background: #fff;
}
.canvasContainer .editorHd {
  padding: 8px;
  border: 1px solid #e6e9ed;
}
.canvasContainer .editorBd {
  flex: 1 1;
}
.canvasContainer .editorSidebar,
.canvasContainer .editorContent {
  display: flex;
  flex-direction: column;
}
.canvasContainer .editorSidebar {
  background: #fff;
}
.canvasContainer .editorSidebar:first-child {
  border-right: 1px solid #e6e9ed;
}
.canvasContainer .editorSidebar:last-child {
  border-left: 1px solid #e6e9ed;
}
.canvasContainer .flow,
.canvasContainer .mind,
.canvasContainer .koni {
  flex: 1 1;
}
.canvasContainer .console {
  height: 100%;
  line-height: 30px;
  padding: 10px;
  border-left: 1px solid #e6e9ed;
}
.canvasContainer .lockIconWrapper {
  height: 50px;
  width: 50px;
}
.canvasContainer .lockIcon {
  position: absolute;
  top: 8px;
  right: 8px;
  line-height: inherit;
  cursor: pointer;
}
.canvasContainer .lockIcon:before {
  content: '';
  width: 0;
  height: 0;
  border-width: 25px;
  border-color: #e5e5e5 #e5e5e5 transparent transparent;
  border-style: solid;
  position: absolute;
  top: -8px;
  left: -42px;
  z-index: 1000;
}
.canvasContainer .icon {
  position: absolute;
  top: -1px;
  right: -4px;
  font-size: 18px;
  z-index: 1003;
}
.canvasContainer .unlock {
  color: #ababab;
  position: absolute;
  top: -1px;
  right: -4px;
  font-size: 18px;
  z-index: 1003;
}
.canvasContainer .lock {
  color: #00f;
  position: absolute;
  top: -1px;
  right: -4px;
  font-size: 18px;
  z-index: 1003;
}
